import Link from 'next/link';
import { useState, useEffect } from 'react';
import Router from 'next/router';
import { withRouter } from 'next/router'; //use router,export component with router
import { getCookie, isLoggedIn } from '../../actions/handleCookie';
import { listJournalForManage,deleteJournal,updateJournal } from '../../actions/journal';
import renderHTML from 'react-render-html'
import moment from 'moment'

const AmendJournal = () => {
	const [journals,setJournals] = useState([])
	const [message,setMessage] = useState('')
	const token = getCookie('token')

	//when component mount
	useEffect(()=>{
		loadJournals()
	},[])

	const loadJournals = () => {
		listJournalForManage().then(data=>{
			if(data.error){
				console.log(data.error)
			} else {
				setJournals(data)
			}
		})
	}

	const showJournals = () => {
		return journals.map((journal,index)=>{
			return (
				<div className="mt-5" key={index}>
				<h3>{journal.title}</h3>
				<p className="mark">Written by {journal.author.name}  || Created at {moment(journal.createdAt).fromNow()} </p>
				</div>
			)
		})
	}

	return (
		<React.Fragment>
			<div className="container">
				<div className="row">
				<div className="col-md-12">
				{showJournals()}
				</div>
				</div>
			</div>
		</React.Fragment>
	)
}

export default AmendJournal